@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:regular&display=swap);
body {
    font: 400 16px "Roboto";
    color: #AAAAAA;
}

h1,
h2,
h4,
h5 {
    color: #1A1A1A;
}

h2 {
    font-size: 48px;
    line-height: 56px;
}

h3 {
    color: #000;
    font-size: 46px;
    line-height: 52px;
}

h4 {
    font-size: 32px;
    line-height: 32px;
}

h5 {
    font-family: "Roboto";
    font-size: 24px;
    line-height: 24px;
}

h6 {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #fff;
}

a {
    letter-spacing: 2px;
    color: #AAAAAA;
}
a:hover {
    text-decoration: underline;
}

p {
    font-size: 24px;
    line-height: 40px;
    margin-bottom: 20px;
}

html, body {
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

main {
    flex: 1 1 auto;
}

[class*=container] {
    max-width: 1440px;
    margin: 0 auto;
}

.header__top {
    background-color: #F5F5F5;
}

.contacts {
    display: flex;
    justify-content: flex-end;
    padding: 10px 0;
}

.contacts__item {
    margin-right: 25px;
}

.header__nav {
    display: flex;
}

.header__white {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 25px 25px 50px;
}

.header__logo:hover {
    opacity: 0.7;
}

.nav__list {
    display: flex;
    margin-left: -100px;
}

.nav__item {
    padding-left: 100px;
}

.location {
    display: flex;
}

.location__list {
    margin-left: -50px;
    display: flex;
}

.location__item {
    padding-left: 50px;
}

.location__link {
    margin-left: 20px;
}

span {
    transition: color 0.5s ease;
}
span:hover {
    opacity: 0.7;
}

.design__content {
    display: flex;
    padding: 100px 0 0 55px;
}

.design__title {
    max-width: 365px;
}

.design__text {
    padding-left: 135px;
    max-width: 670px;
}

.four-rooms__list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 210px;
    margin-left: -25px;
}

.four-rooms__item {
    width: 50%;
    margin-bottom: 70px;
    padding-left: 25px;
}

.foor-rooms__title {
    padding-top: 25px;
}

.brands__list {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-top: 215px;
}

.icons__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.brands__subtitle {
    margin: 30px 0 125px 0;
    color: rgba(74, 74, 74, 0.2901960784);
}

.icons__item {
    width: 16.66%;
    margin-bottom: 50px;
}

.icons__link {
    transition: color 0.5 ease;
}
.icons__link:hover {
    opacity: 0.7;
}

.products {
    display: flex;
    flex-direction: column;
    padding: 85px 55px 0 55px;
}

.products__text {
    padding: 25px 0 75px 0;
    color: #46856E;
    font-size: 14px;
}

.product__list {
    display: flex;
    justify-content: space-between;
}

.product__item {
    width: 33.33%;
}

.product__img {
    text-align: center;
    margin-left: -30%;
}

.products__title {
    max-width: 1080px;
}

.product__subtitle {
    padding: 25px 0;
}

.product__column {
    display: flex;
    flex-direction: column;
}

.product__content {
    max-width: 325px;
    font-size: 16px;
    line-height: 24px;
}

.reviews__title {
    text-align: center;
    padding-top: 240px;
}

.reviews__list {
    padding: 120px 0 120px 200px;
    max-width: 400px;
}

.reviews__items {
    margin-bottom: 40px;
}

.reviews__text {
    color: #fff;
    font-size: 20px;
    line-height: 32px;
    margin: 10px 0;
}

.reviews__icon {
    transition: opacity 0.5s ease 0s;
}
.reviews__icon:hover {
    opacity: 0.7;
}

.reviews__sign {
    color: #fff;
}

.stars__list {
    display: flex;
}

.stars__item {
    transition: opacity 0.5s ease 0s;
}
.stars__item:hover {
    opacity: 0.7;
}

.work__section {
    padding-top: 140px;
}

.gray__title {
    color: #AAAAAA;
    max-width: 880px;
}

.black__title {
    margin: 25px 0 135px 0;
}

.work__items {
    display: flex;
    margin-bottom: 65px;
}

.work__card {
    padding: 258px 90px;
}

.work__text {
    max-width: 310px;
    font-size: 16px;
    margin-top: 25px;
    line-height: 24px;
}

.block2 {
    order: 1;
}

.risk__img {
    margin-bottom: 150px;
}

.risk__content {
    background-color: rgba(242, 242, 242, 0.9490196078);
    background-repeat: 2px;
    width: 1200px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.risk__title {
    padding-top: 65px;
    text-align: center;
}

.risk__list {
    display: flex;
    align-items: center;
    margin: 65px 0;
}
.risk__list.slider .risk__item {
    padding-left: 0;
}

.risk__item {
    padding-left: 250px;
}

.risk__subtitle {
    margin-top: 20px;
    color: #1A1A1A;
    font-size: 12px;
}

.risk__sign {
    margin: 20px 0;
}

.footer {
    background-color: #2B2B2B;
    margin-top: 80px;
}

.footer__section {
    display: flex;
    justify-content: space-between;
    padding: 100px 55px;
    width: 1200px;
    position: relative;
}

.footer__logo {
    transition: opacity 0.5s ease 0s;
}
.footer__logo:hover {
    opacity: 0.7;
}

.about {
    margin-bottom: 45px;
}

.support {
    margin-bottom: 20px;
}

.footer__title {
    margin-bottom: 20px;
}

.addres {
    font-size: 13px;
    line-height: 15px;
}

.social__list {
    display: flex;
}

.social__items {
    padding-left: 15px;
}
.social__items:first-child {
    padding-left: 0;
}

.footer__column4 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.footer__signup {
    font-size: 14px;
}

